import { CheckCircleOutlined, DeleteOutlined, PlusCircleOutlined, PlusOutlined, RedoOutlined } from '@ant-design/icons';
import { Button, Descriptions, Divider, FloatButton, Image, Popconfirm, Select, Space, Table, Tabs, Typography } from "antd";
import { observer } from "mobx-react-lite";
import { productStore } from "../../../store/productStore";
import dayjs from 'dayjs';
import usePaymentPanel from '../../../comp/payment';
import { useNavigate, useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import useCustOrderPanel from '../../../comp/custOrder';
import useMaterialPanel from '../../../comp/material';



function ProductDetail () {

    const {productStatus} = useParams()
    const nav = useNavigate();

    const {product, updateProduct, updateStudio} = productStore;
    useEffect(
        ()=>{
            if(!product.id)
                nav("/product")
        },[product]
    );

    
    const [tabIndex, setTabIndex] = useState(1);



    // update product basic
    const updateStyle = (style) => {

        updateProduct({id: product.id, style})
    }
    const updatemodel = (model) => {

        updateProduct({id: product.id, model})
    }
    const updateImage = (image) => {

        updateProduct({id: product.id, image})
    }


    // payment 
    const paymentPanel = usePaymentPanel({title:'付款记录'});
    const {newPayment, updatePayment, deletePayment} = productStore;
    const submit2NewPayment = () => {
        console.log('new payment item')
        newPayment(product.id, {})
    }
    const submitPayment = (values, callbk) => {
        updatePayment(productStore.product.id, values, callbk)
    }
    const paymentColumns = [
        {
            title: '类别',
            dataIndex: 'title',
            key: 'title',
            width: '3.5em',
            render: (_, {title}) => {
                switch (title) {
                    case '1':
                        return '定金'
                    case '2':
                        return '期金'
                    case '3':
                        return '尾款'
                    default:
                        return '其他'
                }
            }
        },{
            title: '支付日期',
            dataIndex: 'date',
            key: 'date',
            render: (_, {date}) => (
                dayjs(date).format("YYYY.MM.DD") ? dayjs(date).format("YYYY.MM.DD") : "-"
            )

        },{
            title: '支付状态',
            dataIndex: 'paid',
            key: 'paid',
            render: (_, {paid}) => (
                paid ? <><CheckCircleOutlined style={{color: 'green'}} />{'已支付'}</> : <>-</> 
            )
        },{
            title: '支付金额',
            dataIndex: 'amount',
            key: 'amount'
        },{
            title: '操作',
            key: 'action',
            render: (_, record) => (
                <Space>
                    <Popconfirm
                        placement="topRight"
                        title="产品移除"
                        // description={`${item.style}-${item.model}`}
                        onConfirm={()=>deletePayment(productStore.product.id, record.id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <DeleteOutlined  style={{color: 'red'}}/>
                    </Popconfirm>
                     
                    {/* <a onClick={()=>paymentPanel({...record, date: dayjs(record.date)}, submitPayment)}>编辑</a> */}
                </Space>
            )
        },
    ]


    // Cust Order
    const custPanel = useCustOrderPanel({title:'规格 & 数量'});
    const {newCustOrder, updateCustOrder, deleteCustOrder} = productStore;
    const subCustOrder = () => {
        console.log('new payment item')
        newCustOrder(product.id, {})
    }
    const putCutOrder = (values, callbk) => {
        updateCustOrder(productStore.product.id, values, callbk)
    }
    const custColumns = [
        {
            title: '尺码',
            dataIndex: 'size',
            key: 'size',
            width: '3.5em'
        },{
            title: '胸围',
            dataIndex: 's1',
            key: 's1',
            width: '3.5em'
        },{
            title: '腰围',
            dataIndex: 's2',
            key: 's2',
            width: '3.5em'
        },{
            title: '衣长',
            dataIndex: 's3',
            key: 's3',
            width: '3.5em'
        },{
            title: '预定',
            dataIndex: 'preCount',
            key: 'preCount',
            width: '3.5em'
        },{
            title: '出厂',
            dataIndex: 'actCount',
            key: 'actCount',
            width: '3.5em'
        },{
            title: '操作',
            key: 'action',
            render: (_, record) => (
                <Space>
                    <Popconfirm
                        placement="topRight"
                        title="产品移除"
                        // description={`${item.style}-${item.model}`}
                        onConfirm={()=>deleteCustOrder(productStore.product.id, record.id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <DeleteOutlined  style={{color: 'red'}}/>
                    </Popconfirm>
                     
                    {/* <a onClick={()=>custPanel({...record, date: dayjs(record.date)}, putCutOrder)}>编辑</a> */}
                </Space>
            )
        },
    ]



    // Cust Order
    const materialPanel = useMaterialPanel({title:'物料 & 工厂'});
    const {newMaterials, updateMaterials, deleteMaterials} = productStore;
    const subMaterial = () => {
        console.log('new payment item')
        newMaterials(product.id, {})
    }
    const putMaterial = (values, callbk) => {
        updateMaterials(productStore.product.id, values, callbk)
    }
    
    const materialColumns = [
        {
            title: '物料名',
            dataIndex: 'label',
            key: 'label',
        },{
            title: '规格',
            dataIndex: 'specification',
            key: 'specification',
        },{
            title: '单价',
            dataIndex: 'price',
            key: 'price',
        },{
            title: '数量',
            dataIndex: 'count',
            key: 'count',
        },{
            title: '总价',
            dataIndex: 'amount',
            key: 'amount',
        },{
            title: '操作',
            key: 'action',
            render: (_, record) => (
                <Space>
                    <Popconfirm
                        placement="topRight"
                        title="产品移除"
                        // description={`${item.style}-${item.model}`}
                        onConfirm={()=>deleteMaterials(productStore.product.id, record.id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <DeleteOutlined  style={{color: 'red'}}/>
                    </Popconfirm>
                     
                    {/* <a onClick={()=>materialPanel({...record, date: dayjs(record.date)}, putMaterial)}>编辑</a> */}
                </Space>
            )
        },
    ]


    const onClickCustRow = (record) => {
        return {

            ondblclick: (e)=>custPanel({...record, date: dayjs(record.date)}, putCutOrder)
        }
    }
    const onClickMaterialRow = (record) => {
        return {

            ondblclick: (e)=>materialPanel({...record, date: dayjs(record.date)}, putMaterial)
        }
    }
    const onClickPaymentRow = (record) => {
        return {

            ondblclick: (e)=>paymentPanel({...record, date: dayjs(record.date)}, submitPayment)
        }
    }
    
  const items = [
    {
      key: 1,
      label: `规格 & 数量`,
      children:
            <Table pagination={{position:[]}}  showHeader={true} columns={custColumns} dataSource={productStore.product?.custOrders?.map(value=> ({key: `custOrders${value.id}`, ...value }) )} 
            
                summary={
                    (pageData) => {

                        let totalPreCount = 0;
                        let totalActCount = 0

                        pageData.forEach(
                            ({preCount, actCount}) => {
                                totalPreCount += preCount;
                                totalActCount+=actCount;
                            }
                        );

                        return (
                            <Table.Summary.Row>
                                <Table.Summary.Cell index={0}></Table.Summary.Cell>
                                <Table.Summary.Cell index={1}></Table.Summary.Cell>
                                <Table.Summary.Cell index={2}></Table.Summary.Cell>
                                <Table.Summary.Cell index={3}></Table.Summary.Cell>
                                <Table.Summary.Cell index={4}>{totalPreCount > 0 ? totalPreCount : ''}</Table.Summary.Cell>
                                <Table.Summary.Cell index={5}>{totalActCount > 0 ? totalActCount: ''}</Table.Summary.Cell>
                            </Table.Summary.Row>
                        )
                    }
                }
            
                onRow={onClickCustRow}
            />
    },
    {
      key: 2,
      label: `物料 & 工厂`,
      children: 
            <Table pagination={{position:[]}}  showHeader={true} columns={materialColumns} dataSource={productStore.product?.materials?.map(value=> ({key: `materials${value.id}`, ...value }) )} 
                summary={
                    (pageData) => {

                        let total = 0
                        pageData.forEach(
                            ({amount}) => {
                                total += amount;
                            }
                        );

                        return (
                            <Table.Summary.Row>
                                <Table.Summary.Cell index={0}></Table.Summary.Cell>
                                <Table.Summary.Cell index={1}></Table.Summary.Cell>
                                <Table.Summary.Cell index={2}></Table.Summary.Cell>
                                <Table.Summary.Cell index={3}></Table.Summary.Cell>
                                <Table.Summary.Cell index={4}>{total>0?total:''}</Table.Summary.Cell>
                            </Table.Summary.Row>
                        )
                    }
                }
                onRow={onClickMaterialRow}
        
            />
    },
    {
      key: 3,
      label: `付款记录`,
      children:  
            <Table pagination={{position:[]}}  showHeader={true} columns={paymentColumns} dataSource={productStore.product?.payments?.map(value=> ({key: `payments${value.id}`, ...value }) )} 
            summary={
                (pageData) => {

                    let total = 0
                    pageData.forEach(
                        ({amount}) => {
                            total += amount;
                        }
                    );

                    return (
                        <Table.Summary.Row>
                            <Table.Summary.Cell index={0}></Table.Summary.Cell>
                            <Table.Summary.Cell index={1}></Table.Summary.Cell>
                            <Table.Summary.Cell index={2}></Table.Summary.Cell>
                            <Table.Summary.Cell index={3}>{total>0?total:''}</Table.Summary.Cell>
                        </Table.Summary.Row>
                    )
                }
            }
            onRow={onClickPaymentRow}
            />
    },
  ];



    const newRecord = () => {


        switch (tabIndex) {
            case 1:
                subCustOrder()
                break;
            case 2:
                subMaterial()
                break;
            case 3:
                submit2NewPayment()
                break;
        
            default:
                break;
        }
    }

    const getTabsKey = ()=>{


        switch (productStatus) {
            case 'factory':
                return 2;
        
            default:
                return 1;
        }

    }

    const productStatusConvert=(stauts)=>{
        switch (stauts) {
            case "pending":
                return '洽谈中'
            case "studio":
                return  <>
                        样板中 
                        <Divider type='vertical'/>
                        <Popconfirm
                            placement="topRight"
                            title="回滚状态"
                            description={`回退至 洽谈阶段 ?`}
                            onConfirm={()=>productStore.fallbackStatus(product.id)}
                         
                        >
                            <RedoOutlined style={{color: 'red'}}/>
                        </Popconfirm>
                </>
                
            case "factory":
                return <>
                        投产中 
                        <Divider type='vertical'/>
                        <Popconfirm
                            placement="topRight"
                            title="回滚状态"
                            description={`回退至 样板阶段 ?`}
                            onConfirm={()=>productStore.fallbackStatus(product.id)}
                         
                        >
                            <RedoOutlined style={{color: 'red'}}/>
                        </Popconfirm>
                </>
            case "pending":
                return '待尾款'
            default:
                return '-'
        }
    }
    const getOrderCnt = ()=>{
        
        let cnt = 0;
        product.custOrders.forEach(order => {
            cnt+=order.actCount
        });
    

        return cnt 
    }
    const getCostCnt = ()=>{
        let cnt = 0;
        product.materials.forEach(order => {
            cnt+=order.amount
        });
    

        return cnt 
    }
    const getPaymentCnt = ()=>{
        let cnt = 0;
        product.payments.forEach(order => {
            if(order.paid)
            cnt+=order.amount
        });
    

        return cnt 
        
    }

    return (
        <>

            <Space direction="vertical"  style={{ display: 'flex' }}>
                <Space align='top'>
                <Image
                    
                    width={'200px'}
                    height={'300px'}
                    src="error"
                    fallback="data:image/png;base64,"
                />


                    {/* <Descriptions column={{xs:2, s:2}} layout='vertical'> */}
                    <Descriptions  column={1}>
                        <Descriptions.Item label='编号' ><Typography.Text editable={{onChange: updatemodel}} >{product.model}</Typography.Text></Descriptions.Item>
                        <Descriptions.Item label='款式' ><Typography.Text editable={{onChange: updateStyle}} >{product.style}</Typography.Text></Descriptions.Item>
                        {/* <Descriptions.Item label='日期'>{dayjs(product.cdate).format('YYYY.MM.DD')}</Descriptions.Item> */}
                        <Descriptions.Item label='状态'>{productStatusConvert(product.productStatus)}</Descriptions.Item>
                        <Descriptions.Item label='板间'><Select onChange={updateStudio} style={{width: '100%'}} defaultValue={product.sampleOrder?.studioName} options={[{value: 1, label: 701},{value: 2, label: '其他板间'}]}/></Descriptions.Item>
                        <Descriptions.Item label='板师'>{product.sampleOrder?.producerName ? <>{product.sampleOrder.producerName} - {product.sampleOrder.cost}</> : '-' }</Descriptions.Item>
                        {/* <Descriptions.Item  label='备注'>dsfasfasdfasdfsafsajflasfadskflsjflfjsaaldsjf </Descriptions.Item> */}
                        <Descriptions.Item  label='件数'>{getOrderCnt()}</Descriptions.Item>
                        <Descriptions.Item  label='费用'>{getCostCnt()} </Descriptions.Item>
                        <Descriptions.Item  label='支付/总额'>{getPaymentCnt()} / {product.amount}</Descriptions.Item>
                    </Descriptions>
                </Space>

                <Tabs tabBarExtraContent={{right:<Button icon={<PlusOutlined />} onClick={()=>newRecord()} type="primary">新增</Button>}} defaultActiveKey={getTabsKey()} onChange={setTabIndex} items={items} /> 

            </Space>

        </>
    )

}

export default  observer (ProductDetail);
